body {
  height: 100vh;
  padding: 0;
  font-family: sans-serif;
  position: relative;
  overflow: hidden;
}

.header {
  text-align: center;
  grid-area: header;
  position: relative;
}

.mainTitle {
  color: #000;
  text-align: center;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding: 1em;
  font-family: Josefin Sans, sans-serif;
  font-size: 300%;
  font-weight: bold;
  top: 0;
}

#top-toolbar {
  flex-flow: row;
  gap: .5em;
  display: flex;
  position: absolute;
  right: 0;
}

#top-toolbar span {
  flex: 2 auto;
}

.collection {
  height: 100%;
  grid-area: collection;
  grid-template-rows: 2em auto;
  gap: 3px;
  display: grid;
  overflow: scroll;
}

.toolbar {
  text-align: center;
  grid-area: toolbar;
  position: relative;
}

.main {
  grid-area: main;
}

.target {
  grid-area: target;
  grid-template-rows: auto 71px;
  gap: 10px;
  display: grid;
  overflow: hidden;
}

.invariants {
  text-align: center;
  height: 10em;
  grid-area: invariants;
  margin-left: auto;
  margin-right: auto;
  overflow-x: scroll;
}

#grid-container {
  height: 100%;
  background-color: var(--color1);
  grid-template-rows: 4em fit-content(0) auto 10em;
  grid-template-areas: "header header header"
                       "toolbar toolbar toolbar"
                       "collection main target"
                       "collection invariants invariants";
  gap: 10px;
  padding: 10px;
  transition: all .3s;
  display: grid;
}

.full-mode {
  grid-template-columns: 20% calc(60% - 20px) 20%;
}

.collapsed-target {
  grid-template-columns: 20% calc(80% - 20px) 0;
}

.collapsed-target .main {
  margin-right: -10px;
}

.collapsed-collection {
  grid-template-columns: 0 calc(80% - 20px) 20%;
}

.collapsed-collection .main {
  margin-left: -10px;
}

.collapsed-target.collapsed-collection {
  grid-template-columns: 0 calc(100% - 20px) 0;
}

.collapsed-target.collapsed-collection .main {
  margin-left: -10px;
  margin-right: -10px;
}

